import { useNavigate, useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import { cleanup } from "@testing-library/react";

function Detail(props) {
  let { idn } = useParams();
  let book = props.shoes.find((x) => {
    return x.id == idn;
  });
  let navigate = useNavigate();
  let [alert, alertEdit] = useState(false);
  const [inputdata, setInputdata] = useState("");
  let aa = Number(inputdata);

  //aa가 만약에 숫자가 아니라면 경고창 띄우기

  useEffect(() => {
    let a = setTimeout(() => {
      alertEdit((alert = true));
    }, 3000);
    console.log(2);

    return () => {
      console.log(1);
      clearTimeout(a);
    };
  }, []);

  return (
    <>
      <div className="container">
        {console.log(aa)}
        {alert == false ? (
          <div className="alert alert-primary text-center">
            잠시 기다려 주세요.
          </div>
        ) : null}
        {alert == true ? (
          <div className="alert alert-danger text-center">
            <img
              src="https://wcfresource.a1edu.com/newsystem/image/promotion/promotion_220121.jpg?ver=220612090624"
              style={{
                width: "300px",
                borderRadius: "20px",
                backgroundColor: "white",
                padding: "10px",
              }}
            ></img>
            <div className="mt-3">신상품 출시</div>
          </div>
        ) : null}
        <div className="row gx-1">
          <input
            type="text"
            onChange={(e) => {
              setInputdata(e.target.value);
            }}
          ></input>
          {isNaN(aa) == true ? <div>숫자만 입력해 주세요.</div> : null}
        </div>
        <div className="row">
          <div className="col-md-6 text-center">
            <img src={book.img} width="50%" />
          </div>
          <div className="col-md-6">
            <h4 className="pt-5">{book.title}</h4>
            <p>{book.content}</p>
            <button className="btn btn-danger">Start</button>
            <button
              className="btn btn-primary ms-3"
              onClick={() => {
                navigate(-1);
              }}
            >
              Back
            </button>
          </div>
        </div>
      </div>
    </>
  );
}

export default Detail;
