import { useState } from "react";
import "./App.css";
import "./bootstrap/bootstrap.min.css";
import shoesData from "./data/bookData";
import ShoesItem from "./components/ShoesItem";
import { Routes, Route, Link, useNavigate, useParams } from "react-router-dom";
import Detail from "./routes/Detail";
import About from "./routes/About";
import styled from "styled-components";

let fontColor = [
  { name: "blue", color: "#0091FF" },
  { name: "red", color: "#E02020" },
];

function filterVar(obj) {
  return obj.name == color;
}

let color = "red";

let BtnR = styled.button`
  background-color: ${(props) =>
    props.bg == "blue" ? "#0091FF" : props.bg == "red" ? "#E02020" : props.bg};
  color: ${(props) => (props.bg == "red" || "blue" ? "white" : "black")};
  border-radius: 10px;
  border: 0;
  outline: 0;
  padding: 5px 10px 5px 10px;
`;

function App() {
  let [shoes] = useState(shoesData);
  let navigate = useNavigate();

  return (
    <div className="App">
      {console.log(fontColor.filter(filterVar))}
      <nav class="navbar navbar-expand-lg bg-light mb-4">
        <div class="container">
          <a class="navbar-brand">Reading Gate</a>
          <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav me-auto mb-2 mb-lg-0">
              <li class="nav-item">
                <a
                  onClick={() => {
                    navigate("/");
                  }}
                >
                  Home
                </a>
              </li>
              <li class="nav-item">
                <Link to="/detail">Detail</Link>
              </li>
              <li class="nav-item">
                <Link to="/about/one">About</Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      <Routes>
        <Route
          path="*"
          element={<div className="text-center"><div className="mb-3">존재하지 않는 페이지 인걸요.</div><div><BtnR bg="red" onClick={()=>{navigate(-1)}}>돌아가기</BtnR></div></div>}
        ></Route>
        <Route
          path="/"
          element={
            <>
              <div className="container">
                <div className="row">
                  {shoesData.map((a, i) => {
                    return (
                      <div className="col-6 col-md-2">
                        <ShoesItem shoes={shoes} i={i}></ShoesItem>
                      </div>
                    );
                  })}
                </div>
              </div>
            </>
          }
        ></Route>
        <Route
          path="/detail/:idn"
          element={<Detail shoes={shoes}></Detail>}
        ></Route>
        <Route path="/about" element={<About></About>}>
          <Route
            path="one"
            element={
              <>
                <div>첫 주문시 양배추즙 서비스</div>
              </>
            }
          ></Route>
          <Route
            path="two"
            element={
              <>
                <div>생일기념 쿠폰받기</div>
              </>
            }
          ></Route>
        </Route>
      </Routes>
    </div>
  );
}

export default App;
